<template>
    <div class="manage">
        <!-- 用户新增表单 -->
        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="场地/设备编号" prop="selfNum">
                    <el-input placeholder="请输入场地/设备编号" v-model="form.selfNum"></el-input>
                </el-form-item>
                <el-form-item label="场地/设备名" prop="value">
                    <el-input placeholder="请输入场地/设备名" v-model="form.value"></el-input>
                </el-form-item>
                <el-form-item label="场地/设备位置" prop="position">
                    <el-input placeholder="请输入场地/设备位置" v-model="form.position"></el-input>
                </el-form-item>
            </el-form>

            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 页头区域 -->
        <el-form :inline="true" :model="form" class="manage-header">
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    批量删除
                </el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleAdd" style="margin-left: 10px;" type="primary">
                    新增场地
                </el-button>
            </el-form-item>
            <!--form搜索区-->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="请输入场地名" v-model="searchForm.value" suffix-icon="el-icon-search"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary">查询</el-button>
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
        <el-dialog title="提示" :visible.sync="updateDialogVisible" width="50%" :before-close="updateCancel">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
          
               
                <el-form-item label="场地/设备编号" prop="selfNum">
                    <el-input placeholder="请输入场地/设备编号" v-model="form.selfNum"></el-input>
                </el-form-item>
                <el-form-item label="场地/设备名" prop="value">
                    <el-input placeholder="请输入场地/设备名" v-model="form.value"></el-input>
                </el-form-item>
                <el-form-item label="场地/设备位置" prop="position">
                    <el-input placeholder="请输入场地/设备位置" v-model="form.position"></el-input>
                </el-form-item>
           
                
          
            </el-form>

            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="updateCancel">取 消</el-button>
                <el-button type="primary" @click="submitUpdate">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 选项卡 -->
        <!-- <el-tabs v-model="activeName" type="card" @tab-click="handleClick" style="margin-top: 10px; width: 90%;">
                <el-tab-pane label="计量工程中心仪器设备" name="first"></el-tab-pane>
                <el-tab-pane label="EMC中心设备和场地管理" name="second"></el-tab-pane>
                <el-tab-pane label="安规中心仪器设备和场地管理" name="third"></el-tab-pane>
                <el-tab-pane label="安规中心功能性检查设备" name="fourth"></el-tab-pane>
          </el-tabs> -->
        <!-- 主要表格 -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="125" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="250" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="selfNum" label="场地/设备编号" width="250" align="center"></el-table-column>
            <el-table-column prop="value" label="场地/设备名" width="250" align="center"></el-table-column>
            <el-table-column prop="position" label="场地/设备位置" width="250" align="center"></el-table-column>
            <!-- <el-table-column prop="enable" label="启用" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column> -->
            <el-table-column label="操作" width="250" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogVisible: false,
            updateDialogVisible:false,
            viewDialogVisible: false,
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],
            type:"third",
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            searchForm:{
                value: '',
            },
            form: {
                
            },
            rules: {
                value: [
                    { required: true, message: "请输入场地名", trigger: "blur" }
                ],
                position: [
                    { required: true, message: "请输入场地位置", trigger: "blur" }
                ]
            },
        }
    },
    created() {
        this.load()
    },
    methods: {
        reset() {
            this.searchForm.value = ""
            this.load()
        },
        formatTimePeriod(timePeriod) {
            if (timePeriod === 1) {
                return '9:00-12:00';
            } if (timePeriod === 2) {
                return '13:00-17:00';
            }
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/square/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/square/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/square/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    type: this.type,
                    value: this.searchForm.value

                }
            }).then(
                res => {
                    console.log(res)
                    this.tableData = res.data.records
                    this.total = res.data.total
                }
            )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
          //编辑用户表单
          submitUpdate() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.put("/square", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("修改成功")
                            this.updateDialogVisible = false
                            this.load()
                        } else {

                            this.$message.error("修改失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        //弹窗关闭时
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        handleEdit(row) {
            // this.form.role
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            // this.dialogVisible = true
            this.updateDialogVisible = true

        },
        updateCancel() {
            this.updateDialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
       
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.form.type = "third"
                    this.request.post("/square", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("保存成功")
                            this.dialogVisible = false
                            this.load()
                        } else {
                            this.$message.error("保存失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>